@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?buxlet");
  src: url("../fonts/icomoon.eot?buxlet#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?buxlet") format("truetype"), url("../fonts/icomoon.woff?buxlet") format("woff"), url("../fonts/icomoon.svg?buxlet#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-add_circle:before {
  content: "\e900"; }

.icon-arrow_lift:before {
  content: "\e901"; }

.icon-check_circle:before {
  content: "\e902"; }

.icon-close:before {
  content: "\e903"; }

.icon-favorite:before {
  content: "\e904"; }

.icon-keyboard_arrow_right:before {
  content: "\e905"; }

.icon-remove_circle_outline:before {
  content: "\e906"; }

.icon-shopping_cart:before {
  content: "\e907"; }

.icon-thumb_down:before {
  content: "\e908"; }

.icon-thumb_up:before {
  content: "\e909"; }

.icon-mobile:before {
  content: "\e958"; }

.fade-enter-active, .fade-leave-active {
  opacity: 1;
  transition: .3s linear; }

.fade-enter, .fade-leave-active {
  opacity: 0;
  transition: .3s linear; }

.move-enter-active, .move-leave-active {
  transform: translateX(0);
  transition: .3s; }

.move-enter, .move-leave-active {
  transform: translateX(100%);
  transition: .3s; }

.scroll-wrap {
  position: absolute;
  top: 170px;
  bottom: 48px;
  width: 100%;
  overflow: hidden; }



